<template>
<div>
	<div class="title"></div>
	<!---->
	<div style='overflow:hidden'>
		<div class="searchItem">
			<span class="searchTitle"></span>
			<el-select v-model="searchkeys.cateId" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option v-for="item in cateRoleList" :label="item.Fname" :value="item.Fcate_id" :key="item.Fcate_id"></el-option>
            </el-select>
		</div>
		<div class="searchItem">
			<span class="searchTitle"></span>
			<el-input v-model="searchkeys.roleName" @keyup.13.native="search($event)" :maxlength='20' placeholder=""></el-input>
		</div>
		<div class="goSearch">
			<el-button @click="search"></el-button>
      		<el-button @click="clearForm('searchkeys')"></el-button>
		</div>
	</div>

    <div class="tool">
    	<router-link to="category"><el-button size="small"></el-button></router-link>
    	<el-button type="primary" size="small" @click="addRole()"></el-button>
    </div>

	<el-table
	    :data="dataList"
	    border
	    style="width: 100% ; min-height:300px">
	    <el-table-column prop="Frole_id" label="ID"></el-table-column>
	    <el-table-column prop="Fname" label="" ></el-table-column>
	    <el-table-column prop="Fdesc" label="" ></el-table-column>
	    <el-table-column prop="Fcate_name" label=""></el-table-column>
	    <el-table-column  label="" >
	    	<template scope="scope">
	    		<span v-if="scope.row.upStatus"><br><span class="unUseStatus"></span></span>
	    		<span v-else-if="scope.row.Fstatus == '1' "></span>
	    		<span v-else></span>
	        </template>
	    </el-table-column>
	    <el-table-column label="" >
	        <template scope="scope" >
	        	<div v-if="scope.row.Fstatus == 1">
		        	<el-button class='indexFunBtn' type="primary" @click="getEditData(scope.$index)"  size="small"></el-button>
		        	<el-button class='indexFunBtn' type="primary" @click="setPower(scope.row.Frole_id)"  size="small"></el-button>
		        	<el-button class='indexFunBtn' type="danger" @click="setStatusConfirm(scope.row.Frole_id , scope.row.Fstatus)"  size="small"></el-button>
	        	</div>
	        	<div v-else>
	        		<el-button  type="primary" @click="setStatusConfirm(scope.row.Frole_id , scope.row.Fstatus)" :disabled="scope.row.upStatus" size="small"></el-button>
	        	</div>
	        </template>
	    </el-table-column>
	</el-table>
	<div class="pagination">
	    <el-pagination
	      @size-change="handleSizeChange"
	      @current-change="handleCurrentChange"
	      :current-page="currentPage"
	      :page-sizes="[10, 20, 30, 40]"
	      :page-size="10"
	      layout="total, prev, pager, next, jumper"
	      :total="total">
	    </el-pagination>
	</div>
	<!--  -->
	<el-dialog title="" :visible.sync="addDialog">
        <el-form  :model="handelAddRole" :rules="rules" ref="handelAddRole" label-width="100px">
            <el-form-item label="" filterable prop='c_cate_id'>
                <el-select v-model="handelAddRole.c_cate_id" placeholder="">
	                <el-option v-for="item in enableCateRoleList" :label="item.Fname" :value="String(item.Fcate_id)" :key="item.Fcate_id"></el-option>
	            </el-select>
            </el-form-item>
            <el-form-item label=""  prop='c_name'>
                <el-input v-model="handelAddRole.c_name"  :maxlength='40' placeholder=''></el-input>
            </el-form-item>
            <el-form-item label=""  prop='c_desc'>
                <el-input v-model="handelAddRole.c_desc" type="textarea" :rows="3" :maxlength='100'  placeholder=''></el-input>
            </el-form-item>
            <el-form-item class='submitRight'>
                <el-button type="primary" @click="submitnow('handelAddRole')"></el-button>
                <el-button @click="cancelnow"></el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
    <!--  -->
	<el-dialog title="" :visible.sync="editDialog">
        <el-form  :model="handelEditRole" :rules="rules" ref="handelEditRole" label-width="100px">
            <el-form-item label="" filterable prop='e_cate_id'>
                <el-select v-model="handelEditRole.e_cate_id" placeholder="">
	                <el-option v-for="item in enableCateRoleList" :label="item.Fname" :value="String(item.Fcate_id)" :key="item.Fcate_id"></el-option>
	            </el-select>
            </el-form-item>
            <el-form-item label=""  prop='e_name'>
                <el-input v-model="handelEditRole.e_name"  :maxlength='40' placeholder=''></el-input>
            </el-form-item>
            <el-form-item label=""  prop='e_desc'>
                <el-input v-model="handelEditRole.e_desc" type="textarea" :rows="3" :maxlength='100'  placeholder=''></el-input>
            </el-form-item>
            <el-form-item class='submitRight'>
                <el-button type="primary" @click="submitnow('handelEditRole')"></el-button>
                <el-button @click="cancelnow"></el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
</div>
</template>
	

<script>
import api from '../../api/api'
import util from '../../common/util'
export default {
	data() {
		
	    return {
	    	addDialog:false,
	    	editDialog:false,
	    	dataList:[],
	    	cateRoleList:[],
	    	enableCateRoleList:[],
	    	pageIndex:'0',
        	pageSize:'10',
        	currentPage:1,
        	total:0,
        	searchkeys:{
        		"cateId":'',
		        "roleName":""
        	},
        	handelAddRole:{
        		c_cate_id:'',
        		c_name:'',
        		c_desc:'',
        		c_status:'1',
        	},
        	handelEditRole:{
        		'e_name' : '',
    			'e_desc' : '',
    			'e_status' : '1',
    			'e_role_id' : '',
    			'e_cate_id' : ''
        	},
        	rules:{
        		c_cate_id: [{  required: true,  message: ''}],
                c_name: [{  required: true , validator: util.validateName1, trigger: 'blur' }],
                c_desc: [{ required: true , validator: util.validateDesc, trigger: 'blur' }],

                e_cate_id: [{  required: true,  message: ''}],
                e_name: [{  required: true , validator: util.validateName1, trigger: 'blur' }],
                e_desc: [{ required: true , validator: util.validateDesc, trigger: 'blur' }]
            }
	    }
	},
	mounted()  {
		this.showList()
		this.loadInfo()
	},
	methods:{
		handleSizeChange(val) {
	        console.log(` ${val} `)
	    },
	    handleCurrentChange(val) {
	    	this.pageIndex = (val- 1)
	        this.showList()
	    },
	    // 
	    relateFstatus:function(list){
	    	for(var i in list){
	    		if(list[i].cr_status != '1') list[i].Fstatus = '2',list[i].upStatus = true
	    	}
	    	return list
	    },
	    getEnableList:function(list){
	    	let Lists = []
	    	for(var i in list){
	    		if(list[i].Fstatus == '1') Lists.push(list[i])
	    	}
	    	return Lists
	    },
	    loadInfo:function(){
	    	let data = {'pageSize':'9999'}
	    	api.getCateRoleList(data).then(res =>{
	    		if (res.ret != '0') {
                    this.$message(res.retinfo)
                    return
                }
                this.cateRoleList = res.data.list
                this.enableCateRoleList = this.getEnableList(res.data.list)
	    	})
	    },
		showList:function(){
			this.searchkeys.roleName = util.Trim(this.searchkeys.roleName)
			let data = {
		        "s_cate_id": this.searchkeys.cateId,
				"s_role_name":this.searchkeys.roleName,
				'pageIndex':String(this.pageIndex),
				'pageSize':this.pageSize
			}
			api.getRoleList(data).then(res => {
				if (res.ret != '0') {
                    this.$message(res.retinfo)
                    return
                }
                this.dataList = this.relateFstatus(res.data.list)
				this.total = Number(res.data.nums)
			})
		},
		//
		setStatus:function(id , status){
			let data = {
				"s_role_id":id,
        		"s_status": status == '1'?'2':'1'
			}
			api.changeRoleStatus(data).then(res => {
                if (res.ret != '0') {
                    this.$message(res.retinfo)
                    return
                }
                this.$message('')
                this.showList()
            })
		},
		//
		addRole:function(){
			// this.handelAddRole.c_cate_id = ''
			this.handelAddRole.c_name = ''
			this.handelAddRole.c_desc = ''
			this.handelAddRole.c_role_id = ''
			this.addDialog = true
		},
		//
		getEditData:function(index){
			let roleInfo = this.dataList[index]
			this.handelEditRole.e_cate_id = String(roleInfo.Fcate_id)
			this.handelEditRole.e_name = roleInfo.Fname
			this.handelEditRole.e_desc = roleInfo.Fdesc
			this.handelEditRole.e_role_id = roleInfo.Frole_id
            this.editDialog = true
            
		},
		
		//
		setPower:function(id){
			this.$router.push({
				name:'setPower',
				query:{id:id}
			})
		},
		
		//search
		search:function(){
			this.pageIndex = '0'
			this.searchkeys.roleName = util.Trim(this.searchkeys.roleName)
			this.showList()
		},
		clearForm(){
			this.searchkeys.cateId = ''
			this.searchkeys.roleName = ''
			this.showList()
		},
		/*****  /  *****/ 
		submitnow:function(formName){
			if(this.addDialog){
				this.handelAddRole.c_name = util.Trim(this.handelAddRole.c_name)
				this.handelAddRole.c_desc = util.Trim(this.handelAddRole.c_desc)
				
	            this.$refs[formName].validate((valid) => { 
	                if (valid) {
	                    api.addRole(this.handelAddRole).then(res => {
	                        if (res.ret != '0') {
	                            this.$message(res.retinfo)
	                            return
	                        }
	                        this.$message('')
	                        this.showList()
	                        this.addDialog = false
	                    })
	                } else {
	                    console.log('error submit!!')
	                    return false;
	                }
	            })

			}else{
				this.handelEditRole.e_name = util.Trim(this.handelEditRole.e_name)
				this.handelEditRole.e_desc = util.Trim(this.handelEditRole.e_desc)
				this.$refs[formName].validate((valid) => { 
	                if (valid) {
		            	api.editRole(this.handelEditRole).then(res => {
			                if (res.ret != '0') {
			                    this.$message(res.retinfo)
			                    return
			                }
			                this.$message('')
		                    this.showList()
		                    this.editDialog = false
			            })
			        } else {
	                    console.log('error submit!!')
	                    return false;
	                }
	            })
			}
		},
		setStatusConfirm:function(id , status){
			let warnInfo = {
				title  : status == '1'?'':''
			}
			const h = this.$createElement
			if(status == '1'){
                warnInfo.content = h('div',null,[
                        h('p',{style:'margin-bottom:6px'},''),
                        h('p',{style:'color:#ff4949'},' '),
                        h('p',{style:'margin-top:6px'},'')
                    ])
            }else{
                warnInfo.content = h('div',null,[
                        h('p',{style:'margin-bottom:6px'},''),
                        h('p',{style:'color:#ff4949'},' '),
                        h('p',{style:'margin-top:6px'},'')
                    ])
            }
	        this.$confirm(warnInfo.content, warnInfo.title, {
	          //type: 'warning'`
	        }).then(() => {
	        	this.setStatus(id , status)
	        }).catch(() => {})
      	},
		cancelnow:function(id){
			this.addDialog = false
			this.editDialog = false
		}
	}


}
</script>


