<template>
<div>
    <div class="title"> / </div>
	<!---->
    <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input v-model="filters.strStoreId" @keyup.13.native="search($event)" placeholder="ID" style="min-width: 240px;"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input v-model="filters.strStoreName" @keyup.13.native="search($event)" placeholder="" style="min-width: 240px;"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search"></el-button>
          <el-button @click="clearForm()"></el-button>
        </el-form-item>
    </el-form>

    <div class="tool">
    	<router-link to="add_store"><el-button type="primary" size="small"></el-button></router-link>
    </div>

	<el-table
	    :data="dataList"
	    border
	    style="width: 100% ; min-height:300px">
	    <el-table-column prop="strStoreId" label="ID" >
	    	
	    </el-table-column>
	    <el-table-column prop="strStoreName" label="" ></el-table-column>
	    <el-table-column prop="strProvinceName" label="">
	    	<template scope="scope">
	            {{scope.row.strProvinceName + scope.row.strCityName + scope.row.strAreaName +scope.row.strAddress}}
	        </template>
	    </el-table-column>
	    <el-table-column prop="strChannelName" label="" ></el-table-column>
	    <el-table-column prop="strChannelManagerName" label="" ></el-table-column>
	    <el-table-column  label="" >
	    	<template scope="scope">
	            {{scope.row.strStatus == 1 ?'':''}}
	        </template>
	    </el-table-column>
	    <el-table-column 
	        label=""
	        >
	        <template scope="scope">
	        	<el-button class = 'indexFunBtn' type="primary" @click="editStore(scope.row.strStoreId)"  size="small"></el-button>
	        	<el-button class = 'indexFunBtn' type="danger" @click="disableStore(scope.row.strStoreId)"  size="small"></el-button>
	        	<el-button class = 'indexFunBtn' type="primary" @click="enableStore(scope.row.strStoreId)"  size="small"></el-button>
	        </template>
	    </el-table-column>
	</el-table>
	<div class="pagination">
	    <el-pagination
	      @size-change="handleSizeChange"
	      @current-change="handleCurrentChange"
	      :current-page="currentPage4"
	      :page-sizes="[10, 20, 30, 40]"
	      :page-size="10"
	      layout="total, prev, pager, next, jumper"
	      :total="total">
	    </el-pagination>
	</div>
</div>
</template>
	

<script>
import api from '../../api/api'
import util from '../../common/util'
export default {
	data() {
	    return {
	    	dataList:[],
	    	pageIndex:'0',
        	pageSize:'10',
        	currentPage4:1,
        	total:0,
        	filters:{
        		'strStoreId':'',
        		'strStoreName':''
        	}
	        
	    }
	},

	methods:{
		handleSizeChange(val) {
	        console.log(` ${val} `)
	    },
	    handleCurrentChange(val) {
	    	this.pageIndex = (val- 1) * 10
	        this.showList()
	    },
		showList:function(){
			let data ={
				pageinfo:{
					'pageIndex':String(this.pageIndex),
					'pageSize':this.pageSize,
				},
				'searchkeys':this.filters
			}
			api.getStoreList(data).then(res => {
				if (res.ret != '0') {
                    this.$layer.alert(res.retinfo)
                    return
                }
				this.dataList = res.data.list
				this.total = res.data.nums
			})
		},
		//
		editStore:function(strStoreId){
			this.$router.push({
				name:'editStore',
				query:{id:strStoreId}
			})
		},
		// 
		disableStore:function(strStoreId){
			api.disableStore({strStoreId:strStoreId}).then(res => {
				if (res.ret != '0') {
                    this.$layer.alert(res.retinfo)
                    return
                }
				this.$message({
	                message: '',
	                type: 'success'
	            });
	            this.showList()
			})

		},
		enableStore:function(strStoreId){
			api.enableStore({strStoreId:strStoreId}).then(res => {
				if (res.ret != '0') {
                    this.$layer.alert(res.retinfo)
                    return
                }
				this.$message({
	                message: '',
	                type: 'success'
	            });
	            this.showList()
			})

		},
		//search
		search:function(){
			console.log(364)
			this.filters.strStoreId = util.Trim(this.filters.strStoreId)
			this.filters.strStoreName = util.Trim(this.filters.strStoreName)
			this.showList()
		},
		clearForm(){
			this.filters.strStoreId = ''
			this.filters.strStoreName = ''
			this.showList()
		},
		// 
	},
	mounted()  {
		this.showList()
	}


}
</script>


