<template>
<div>
    <div class="title">Dealer > Dealer</div>
    <div class="content-container">
        <div class="tool">
            <router-link to="index"><el-button type="primary" size="small">dealer</el-button></router-link>
        </div>
        <el-form  :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px">
            <p>1.</p>
            <el-form-item label="" prop='dealer.strDealerName' >
                <el-input v-model="ruleForm.dealer.strDealerName" placeholder='' ></el-input>
            </el-form-item>

            <el-form-item label="" class = 'mustStar'>
                <el-col :span="8">
                    <el-form-item prop="dealer.strAddrProvinceId">
                        <el-select v-model="ruleForm.dealer.strAddrProvinceId" filterable placeholder="">
                            <el-option  v-for="item in dealer.provinces"  :label="item.strProvinceName"  :value="item.strProvinceId" :key="item.strProvinceId">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item prop="dealer.strAddrCityId">
                        <el-select v-model="ruleForm.dealer.strAddrCityId" filterable placeholder="">
                            <el-option  v-for="item in dealer.citys"  :label="item.strCityName"  :value="item.strCityId" :key="item.strCityId">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item prop="dealer.strAddrAreaId">
                        <el-select v-model="ruleForm.dealer.strAddrAreaId" filterable placeholder="">
                            <el-option  v-for="item in dealer.areas"  :label="item.strAreaName"  :value="item.strAreaId" :key="item.strAreaId">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-form-item>
            <el-form-item  prop='dealer.strAddress'>
                <el-input v-model="ruleForm.dealer.strAddress" placeholder=""></el-input>
            </el-form-item>

            <el-form-item label="" prop='dealer.strLicenseNum' >
                <el-input v-model="ruleForm.dealer.strLicenseNum" :maxlength='18' placeholder='1518'></el-input>
            </el-form-item>

            <el-form-item label="" >
                <el-upload class="avatar-uploader" :name="fileName" :action=" apiRoot + '/upload ' " accept=".jpg,.png,.jpeg" :show-file-list="false" :on-success="handleAvatarSuccess1" :before-upload="beforeAvatarUpload">
                    <img v-if="imageUrl1" :src="imageUrl1" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>

            <p>2.</p>
            <el-form-item label="" prop='dealer.account_bank.strAccountNum' >
                <el-input v-model="ruleForm.dealer.account_bank.strAccountNum" :type='number' :maxlength='30' placeholder='10~30'></el-input>
            </el-form-item>
            <el-form-item label="" prop='dealer.account_bank.strAccountName' >
                <el-input v-model="ruleForm.dealer.account_bank.strAccountName" placeholder='' ></el-input>
            </el-form-item>
            <el-form-item label="" class = 'mustStar'>
                <el-col :span="8">
                    <el-form-item prop="dealer.account_bank.strAddrProvinceId">
                        <el-select v-model="ruleForm.dealer.account_bank.strAddrProvinceId" filterable placeholder="">
                            <el-option  v-for="item in bankAddr.provinces"  :label="item.strProvinceName"  :value="item.strProvinceId" :key="item.strProvinceId">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item prop="dealer.account_bank.strAddrCityId">
                        <el-select v-model="ruleForm.dealer.account_bank.strAddrCityId" filterable placeholder="">
                            <el-option  v-for="item in bankAddr.citys"  :label="item.strCityName"  :value="item.strCityId" :key="item.strCityId">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item prop="dealer.account_bank.strAddrAreaId">
                        <el-select v-model="ruleForm.dealer.account_bank.strAddrAreaId" filterable placeholder="">
                            <el-option  v-for="item in bankAddr.areas"  :label="item.strAreaName"  :value="item.strAreaId" :key="item.strAreaId">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-form-item>
            <el-form-item label=""  prop="dealer.account_bank.strBankId">
                <el-select filterable v-model="ruleForm.dealer.account_bank.strBankId" placeholder="">
                    <el-option v-for="item in bankList" :label='item.strName' :value="item.strId" :key="item.strId"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop='dealer.account_bank.strBranchName' >
                <el-input v-model="ruleForm.dealer.account_bank.strBranchName" placeholder='' ></el-input>
            </el-form-item>

            <p>3.</p>
            <el-form-item label="" prop='dateRange' >
                <div class="block">
                    <el-date-picker
                      v-model="ruleForm.dateRange"
                      :default-value="ruleForm.dateRange"
                      range-separator=" ~ "
                      type="daterange"
                      :clearable='false'
                      placeholder="">
                    </el-date-picker>
                </div>
            </el-form-item>
            

            <el-form-item label="" >
                <el-upload class="avatar-uploader" :name="fileName" :action=" apiRoot + '/upload ' " accept=".jpg,.png,.jpeg" :show-file-list="false" :on-success="handleAvatarSuccess2" :before-upload="beforeAvatarUpload">
                    <img v-if="imageUrl2" :src="imageUrl2" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>

            <el-form-item class='textRight'>
                <el-button type="primary" @click="submitnow('ruleForm')"></el-button>
                <el-button @click="submitnow('ruleForm',true)"></el-button>
                <el-button @click="cancelnow"></el-button>
            </el-form-item>
            
        </el-form>
        
    </div>

    </div>	
</template>

<script type="text/javascript">
	import api from '../../api/api';
	import util from '../../common/util';
	export default {
		data() {
            var validateName = (rule, value, callback) => {
                value = util.Trim(value)
                let len = value.gblen()
                if( len < 12|| len >100){
                    callback(new Error('12~100'))
                }else if( util.checkSpecialWord(value)){
                    callback(new Error(''))
                }
                callback()
            }
            var validateAccountName = (rule, value, callback) => {
                let len = value.gblen()
                if( len < 4|| len >100){
                    callback(new Error('4~100'))
                }
                callback()
            }
            var validateBranchName = (rule, value, callback) => {
                let len = value.gblen()
                if( len < 4|| len >20){
                    callback(new Error('4~20'))
                }
                callback()
            }
            var validateDateRange = (rule, value, callback) => {
                if(!value[0]){
                    callback(new Error(''))
                }
                callback()
            }
		    return {
                apiRoot:util.api,
                bankList:[],
                pickerOptions: {
                  disabledDate(time) {
                    return false
                    // return time.getTime() < Date.now() - 8.64e7;
                  }
                },
                fileName:'fileName',
                number:'number',
                imageUrl1: '',//
                imageUrl2: '',
                dealer:{
                    provinces:'',
                    citys:'',
                    areas:''
                },
                bankAddr:{
                    provinces:'',
                    citys:'',
                    areas:'',
                },
	            ruleForm:{
                    'dateRange':[],
                    'dealer':{
                        'strDealerName':'',
                        'strAddrProvinceId':'',
                        'strAddrCityId':'',
                        'strAddrAreaId':'',
                        'strAddress':'',
                        'strLicenseNum':'',
                        'strLicensePic':'',
                        'account_bank':{
                            'strAccountNum':'',
                            'strAccountName':'',
                            'strAddrProvinceId':'',
                            'strAddrCityId':'',
                            'strAddrAreaId':'',
                            'strBankId':'',
                            'strBranchName':''
                        },
                        'strContractBeginTime':'',
                        'strContractEndTime':'',
                        'strContractPic':'',
                        'strStatus':'3'
                    }
                     
	            },
                rules:{
                    'dealer.strAddrProvinceId': [
                        {  required: true, message: '', trigger: 'change' }
                    ],
                    'dealer.strAddrCityId': [
                        {  required: true, message: '', trigger: 'change' }
                    ],
                    'dealer.strAddrAreaId': [
                        {  required: true, message: '', trigger: 'change' }
                    ],
                    'dealer.strDealerName': [
                        {  required: true , validator: validateName, trigger: 'blur' }
                    ],
                    'dealer.strAddress': [
                        {  required: true , validator: util.validateAddr, trigger: 'blur' }
                    ],
                    'dealer.strLicenseNum': [
                        {  required: true , validator: util.validateLicenseNum, trigger: 'blur' }
                    ],

                    'dealer.account_bank.strAccountNum': [
                        {  required: true , validator: util.validateAccountNum, trigger: 'blur' }
                    ],
                    'dealer.account_bank.strAccountName': [
                        {  required: true , validator: validateAccountName, trigger: 'blur' }
                    ],
                    'dealer.account_bank.strBranchName': [
                        {  required: true , validator: validateBranchName, trigger: 'blur' }
                    ],

                    'dealer.account_bank.strAddrProvinceId': [
                        {  required: true, message: '', trigger: 'change' }
                    ],
                    'dealer.account_bank.strAddrCityId': [
                        {  required: true, message: '', trigger: 'change' }
                    ],
                    'dealer.account_bank.strAddrAreaId': [
                        {  required: true, message: '', trigger: 'change' }
                    ],
                    'dealer.account_bank.strBankId': [
                        {  required: true, message: '', trigger: 'change' }
                    ],
                    'dateRange': [
                        {required: true, validator: validateDateRange, trigger: 'blur' }
                    ]
                    
                }
		    }
		},
        watch: {
            // 
            'ruleForm.dealer.strAddrProvinceId': function(val, oldVal) {
                this.ruleForm.dealer.strAddrCityId = ''
                this.getCitys(val)
            },
            'ruleForm.dealer.strAddrCityId': function(val) {
                this.ruleForm.dealer.strAddrAreaId = ''
                this.getAreas(val)
            },
            'ruleForm.dealer.account_bank.strAddrProvinceId': function(val, oldVal) {
                this.ruleForm.dealer.account_bank.strAddrCityId = ''
                this.getCitys(val , 'bank')
            },
            'ruleForm.dealer.account_bank.strAddrCityId': function(val) {
                this.ruleForm.dealer.account_bank.strAddrAreaId = ''
                this.getAreas(val , 'bank')
            }
        },
		mounted()  {
            this.loadInfo()
		},
		methods:{
            handleAvatarSuccess1(res, file) {
                this.ruleForm.dealer.strLicensePic = file.response.fileName
                this.imageUrl1 = URL.createObjectURL(file.raw);
            },
            handleAvatarSuccess2(res, file) {
                this.ruleForm.dealer.strContractPic = file.response.fileName
                this.imageUrl2 = URL.createObjectURL(file.raw);
            },
            beforeAvatarUpload(file) {
                const isJpeg = file.type === 'image/jpeg'
                const isPng = file.type === 'image/png'
                const isJPG = file.type === 'image/jpg'
                const isLt1M = file.size / 1024 / 1024 < 1
                var flags
                if ((!isJpeg) && (!isPng) && (!isJPG)) {
                    flags = false
                    this.$message.error('.jpg .png .jpeg!')
                }
                if (!isLt1M) {
                  this.$message.error(' 1MB!')
                }
                return flags && isLt1M
            },
            loadInfo: function() {
                api.getBank({}).then(res => {
                    if (res.ret != '0') {
                        this.$message(res.retinfo)
                        return
                    }
                    this.bankList = res.data.bankList
                })
                api.getAddress({}).then(res => {
                    if (res.ret != '0') {
                        this.$message(res.retinfo)
                        return
                    }
                    this.dealer.provinces = res.data.address
                    this.bankAddr.provinces = res.data.address
                })
            },
			//
            cancelnow: function() {
                this.$router.push({ path: '/Dealer/index' })
            },

            //
            submitnow(formName , GoOn) {
                let self = this
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        // if(!this.ruleForm.dealer.strLicensePic){
                        //     this.$layer.alert('')
                        //     return
                        // } 
                        // if(!this.ruleForm.dealer.strLicensePic){
                        //     this.$layer.alert('')
                        //     return
                        // }
                        this.ruleForm.dealer.strDealerName = util.Trim(this.ruleForm.dealer.strDealerName)
                        this.ruleForm.dealer.strContractBeginTime = this.ruleForm.dateRange[0]
                        this.ruleForm.dealer.strContractEndTime = this.ruleForm.dateRange[1]
                        api.addDealerLogic(this.ruleForm).then(res => {
                            if (res.ret != '0') {
                                self.$message(res.retinfo)
                                return
                            }
                            this.$message('')
                            if(!GoOn) {
                                self.$router.push({ path: '/Dealer/index' })
                            }else {
                                self.$refs['ruleForm'].resetFields()
                                this.imageUrl1 = ''
                                this.imageUrl2 = ''
                                this.ruleForm.dealer.strLicensePic = ''
                                this.ruleForm.dealer.strContractPic = ''
                                this.dealer.citys = []
                                this.dealer.areas = []
                                this.bankAddr.citys = []
                                this.bankAddr.areas = []
                            }

                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                })
                
            },
            // 
            getCitys: function(val , bank) {
                if(bank == 'bank'){
                    var bankProvinces = this.bankAddr.provinces
                    for (var index in bankProvinces) {
                        if (bankProvinces[index].strProvinceId == val) {
                            this.bankAddr.citys = bankProvinces[index].citys
                        }
                    }
                }else{
                    var dealerProvinces = this.dealer.provinces
                    for (var index in dealerProvinces) {
                        if (dealerProvinces[index].strProvinceId == val) {
                            this.dealer.citys = dealerProvinces[index].citys
                        }
                    }
                }
            },
            // 
            getAreas: function(val , bank) {
                if(bank == 'bank'){
                    var bankCitys = this.bankAddr.citys
                    for (var index in bankCitys) {
                        if (bankCitys[index].strCityId == val) {
                            this.bankAddr.areas = bankCitys[index].areas
                        }
                    }
                }else{
                    var dealerCitys = this.dealer.citys
                    for (var index in dealerCitys) {
                        if (dealerCitys[index].strCityId == val) {
                            this.dealer.areas = dealerCitys[index].areas
                        }
                    }
                }
            }

            


		}
	}
</script>


