<template>
<div>
	<div class="title">
		<el-col :span="12"></el-col>
		<el-col :span="12" class="textRight"><router-link to="index"><el-button size="small"></el-button></router-link></el-col>
	</div>
	<!---->
	<div style='overflow:hidden'>
		<div class="searchItem">
			<span class="searchTitle"></span>
			<el-select v-model="searchkeys.s_system_id" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option v-for="item in systemList" :label="item.name" :value="item.system_id" :key="item.system_id"></el-option>
            </el-select>
		</div>
		<div class="searchItem">
			<span class="searchTitle"></span>
			<el-input v-model="searchkeys.s_module_name" @keyup.13.native="search($event)" :maxlength="40" placeholder=""></el-input>
		</div>
		<div class="goSearch">
			<el-button @click="search"></el-button>
      		<el-button @click="clearForm('searchkeys')"></el-button>
		</div>
	</div>

    <div class="tool">
    	<el-button type="primary" size="small" @click="Add()"></el-button>
    </div>

    <table class="el-table el-table--border border-RB-none" cellspacing="0" cellpadding="0" border="0">
    <tr>
    	<th width="15%" class="is-leaf"><div class="cell"></div></th>
    	<th width="15%" class="is-leaf"><div class="cell"></div></th>
    	<th width="40%" class="is-leaf"><div class="cell"></div></th>
    	<th width="15%" class="is-leaf"><div class="cell"></div></th>
    	<th width="15%" class="is-leaf"><div class="cell"></div></th>
    </tr>
    <tr v-for="(item , index) in dataList">
	    <!-- <td width="3%">{{ $index + 1 }}</td> -->
	    <td  :rowspan="item.Fsystem_idSpan" :class="{hidden: item.Fsystem_idDis}"><div class="cell">{{item.Fsystem_name}}</div></td>
	    <td  :rowspan="item.Fauthority_idSpan" :class="{hidden: item.Fauthority_idDis}"><div class="cell">{{item.Fname}}</div></td>
	    <td><div class="cell">{{item.Fdesc}}</div></td>
	    <td><div class="cell">
		    	<span v-if="item.upStatus"><br><span class="unUseStatus"></span></span>
	    		<span v-else-if="item.Fstatus == '1' "></span>
	    		<span v-else></span>
    		</div>
	    </td>
	    <td>
	    	<div class="cell" v-if="item.Fstatus == 1">
	        	<el-button class='indexFunBtn' type="primary" @click="getEditData(index)"  size="small"></el-button>
	        	<el-button class='indexFunBtn' type="danger" @click="setStatusConfirm(item.Fauthority_id , item.Fstatus)"  size="small"></el-button>
        	</div>
        	<div class="cell" v-else>
        		<el-button  type="primary" @click="setStatusConfirm(item.Fauthority_id , item.Fstatus)" :disabled="item.upStatus" size="small"></el-button>
        	</div>
	    </td>
	    
	</tr>
    </table>
	<div class="pagination">
	    <el-pagination
	      @size-change="handleSizeChange"
	      @current-change="handleCurrentChange"
	      :current-page="currentPage"
	      :page-sizes="[10, 20, 30, 40]"
	      :page-size="10"
	      layout="total, prev, pager, next, jumper"
	      :total="total">
	    </el-pagination>
	</div>
	<!--  -->
	<el-dialog title="" :visible.sync="addDialog">
        <el-form  :model="addModule" :rules="rules" ref="addModule" label-width="100px">
            <el-form-item label="" prop='c_system_id'>
                <el-select v-model="addModule.c_system_id" placeholder="">
	                <el-option v-for="item in enableSystemList" :label="item.name" :value="item.system_id" :key="item.system_id"></el-option>
	            </el-select>
            </el-form-item>
            <el-form-item label=""  prop='c_name'>
                <el-input :maxlength='40' v-model="addModule.c_name"  placeholder=''></el-input>
            </el-form-item>
            <el-form-item label=""  prop='c_desc'>
                <el-input type="textarea" :rows="3" :maxlength='100' v-model="addModule.c_desc"  placeholder=''></el-input>
            </el-form-item>
            <el-form-item class='submitRight'>
                <el-button type="primary" @click="submitnow('addModule')"></el-button>
                <el-button @click="cancelnow"></el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
    <!--  -->
	<el-dialog title="" :visible.sync="editDialog">
        <el-form  :model="editModule" :rules="rules" ref="editModule" label-width="100px">
            <el-form-item label="" prop='e_system_id'>
                <el-select v-model="editModule.e_system_id" placeholder="" disabled>
	                <el-option v-for="item in systemList" :label="item.name" :value="item.system_id" :key="item.system_id"></el-option>
	            </el-select>
            </el-form-item>
            <el-form-item label=""  prop='e_name'>
                <el-input :maxlength='40' v-model="editModule.e_name"  placeholder=''></el-input>
            </el-form-item>
            <el-form-item label=""  prop='e_desc'>
                <el-input type="textarea" :rows="3" :maxlength='100' v-model="editModule.e_desc"  placeholder=''></el-input>
            </el-form-item>
            <el-form-item class='submitRight'>
                <el-button type="primary" @click="submitnow('editModule')"></el-button>
                <el-button @click="cancelnow"></el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
</div>
</template>

<script>
import api from '../../api/api'
import util from '../../common/util'
export default {
	data() {
		var validateName = (rule, value, callback) => {
            value = util.Trim(value)
            let len = value.gblen()
            if( len < 1|| len >40){
                callback(new Error('40'))
            }else if( util.checkSpecialWord(value)){
                callback(new Error(''))
            }
            callback()
        }
        var validateDesc = (rule, value, callback) => {
            value = util.Trim(value)
            let len = value.gblen()
            if( len < 1|| len >100){
                callback(new Error('100'))
            }
            callback()
        }
	    return {
	    	addDialog:false,
	    	editDialog:false,
	    	dataList:[],
	    	systemList:[],
	    	enableSystemList:[],
	    	pageIndex:'0',
        	pageSize:'10',
        	currentPage:1,
        	total:0,
        	searchkeys:{
        		"s_system_id":'',
		        "s_module_name":""
        	},
        	addModule:{
        		"c_system_id":"",
				"c_type":"3",
				"c_desc":"",
				"c_name":""
        	},
        	editModule:{
        		"e_authority_id":"",
				"e_system_id":"",
				"e_type":"3",
				"e_desc":"",
				"e_name":""
        	},
        	rules:{
        		c_system_id: [{  required: true,  message: ''}],
                c_name: [{  required: true , validator: validateName, trigger: 'blur' }],
                c_desc: [{ required: true , validator: validateDesc, trigger: 'blur' }],

                e_system_id: [{  required: true,  message: ''}],
                e_name: [{  required: true , validator: validateName, trigger: 'blur' }],
                e_desc: [{ required: true , validator: validateDesc, trigger: 'blur' }]
            }
	    }
	},
	mounted()  {
		this.showList()
		this.loadInfo()
	},
	methods:{
		handleSizeChange(val) {
	        console.log(` ${val} `)
	    },
	    handleCurrentChange(val) {
	    	this.pageIndex = (val- 1)
	        this.showList()
	    },
	    
	    loadInfo:function(){
	    	let data = {'pageSize':'9999'}
	    	api.getSystemList(data).then(res =>{
	    		if (res.ret != '0') {
                    this.$message(res.retinfo)
                    return
                }
                this.systemList = res.data.list
                this.enableSystemList = this.getEnableList(res.data.list)
                
	    	})
	    },
	    // 
	    relateFstatus:function(list){
	    	for(var i in list){
	    		if(list[i].system_status != '1') list[i].Fstatus = '2',list[i].upStatus = true
	    	}
	    	return list
	    },
	    getEnableList:function(list){
	    	let systemList = []
	    	for(var i in list){
	    		if(list[i].status == '1') systemList.push(list[i])
	    	}
	    	return systemList
	    },
	    setRowspan:function(list){
	    	let arr = []
	    	for(var m in list){
	    		let modules = list[m].system_module_list
	    		for(var n in modules ){
	    			arr.push(modules[n])
	    		}
	    	}
	    	return arr
	    },
		showList:function(){
			
			let data = {
		        "s_system_id":this.searchkeys.s_system_id,
				"s_module_name":this.searchkeys.s_module_name,
				'pageIndex':String(this.pageIndex),
				'pageSize':this.pageSize
			}
			api.getModuleList(data).then(res => {
				if (res.ret != '0') {
                    this.$message(res.retinfo)
                    return
                }
                
                this.dataList =util.combineCell(this.relateFstatus(this.setRowspan(res.data.list)))
				this.total = Number(res.data.nums)
			})
		},
		//
		setStatus:function(id , status){
			console.log(id , status)
			let data = {
				"e_type":this.editModule.e_type,
				"e_authority_id":id,
        		"e_status": status == '1'?'2':'1'
			}
			api.changeAuthStatus(data).then(res => {
                if (res.ret != '0') {
                    this.$message(res.retinfo)
                    return
                }
                this.$message('')
                this.showList()
            })
		},
		//
		Add:function(){
			// this.addModule.c_system_id = ''
			this.addModule.c_name = ''
			this.addModule.c_desc = ''
			this.addDialog = true
		},
		//
		getEditData:function(index){
			let roleInfo = this.dataList[index]
			this.editModule.e_authority_id = String(roleInfo.Fauthority_id)
			this.editModule.e_name = roleInfo.Fname
			this.editModule.e_desc = roleInfo.Fdesc
			this.editModule.e_system_id = roleInfo.Fsystem_id
            this.editDialog = true
		},
		//search
		search:function(){
			this.searchkeys.s_module_name = util.Trim(this.searchkeys.s_module_name)
			this.showList()
		},
		clearForm(){
			this.searchkeys.s_system_id = ''
			this.searchkeys.s_module_name = ''
			this.showList()
		},
		/*****  /  *****/ 
		submitnow:function(formName){
			if(this.addDialog){
				this.addModule.c_name = util.Trim(this.addModule.c_name)
				this.addModule.c_desc = util.Trim(this.addModule.c_desc)
				
	            this.$refs[formName].validate((valid) => { 
	                if (valid) {
	                    api.addAuth(this.addModule).then(res => {
	                        if (res.ret != '0') {
	                            this.$message(res.retinfo)
	                            return
	                        }
	                        this.$message('')
	                        this.showList()
	                        this.addDialog = false
	                    })
	                } else {
	                    console.log('error submit!!')
	                    return false;
	                }
	            })

			}else{
				this.editModule.e_name = util.Trim(this.editModule.e_name)
				this.editModule.e_desc = util.Trim(this.editModule.e_desc)
				this.$refs[formName].validate((valid) => { 
	                if (valid) {	
		            	api.editAuth(this.editModule).then(res => {
			                if (res.ret != '0') {
			                    this.$message(res.retinfo)
			                    return
			                }
			                this.$message('')
		                    this.showList()
		                    this.editDialog = false
			            })
			        } else {
	                    console.log('error submit!!')
	                    return false;
	                }
	            })
			}
		},
		setStatusConfirm:function(id , status){
			let warnInfo = {
				title  : status == '1'?'':''
			}
			const h = this.$createElement
			if(status == '1'){
                warnInfo.content = h('div',null,[
                        h('p',{style:'margin-bottom:6px'},''),
                        h('p',{style:'color:#ff4949'},' '),
                        h('p',{style:'color:#ff4949'},' '),
                        h('p',{style:'margin-top:6px'},'')
                    ])
            }else{
                warnInfo.content = h('div',null,[
                        h('p',{style:'margin-bottom:6px'},''),
                        h('p',{style:'color:#ff4949'},' '),
                        h('p',{style:'margin-top:6px'},'')
                    ])
            }
	        this.$confirm(warnInfo.content, warnInfo.title, {
	          //type: 'warning'`
	        }).then(() => {
	        	this.setStatus(id , status)
	        }).catch(() => {})
      	},
		cancelnow:function(id){
			this.addDialog = false
			this.editDialog = false
		},
	}


}
</script>


