<template>
<div>
<div class="title"> / </div>
<div class="">
    <el-tabs type="border-card">
        <el-tab-pane label="" class='channelInfo'>
            <el-form ref="form" :model="form" label-width="180px" :rules="rules">
                <el-form-item label="">
                    <el-select v-model="form.strLevelId" filterable placeholder="">
                        <el-option  v-for="item in channelTemplateList"  :label="item.strTemplateName"  :value="item.strTemplateId" :key="item.strTemplateId">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop='strFullName'>
                    <el-input v-model="form.strFullName" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="/">
                    <el-select v-model="form.strPartner_id" placeholder="">
                        <el-option  v-for="item in partnerList"  :label="item.strPartnerName"  :value="item.strPartnerId" :key="item.strPartnerId">
                        </el-option>
                    </el-select>
                </el-form-item>
                <!-- <el-form-item label="">
                    <el-select v-model="form.strSecond_channel" placeholder="">
                        <el-option label="" value="0"></el-option>
                        <el-option label="" value="1"></el-option>
                    </el-select>
                </el-form-item> -->


                <el-form-item label="">
                    <el-col :span="6">
                        <el-form-item prop="strAddr_province_id">
                            <el-select v-model="form.strAddr_province_id" filterable placeholder="">
                                <el-option  v-for="item in provinces"  :label="item.strProvinceName"  :value="item.strProvinceId" :key="item.strProvinceId">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item prop="strAddr_city_id">
                            <el-select v-model="form.strAddr_city_id" filterable placeholder="">
                                <el-option  v-for="item in citys"  :label="item.strCityName"  :value="item.strCityId" :key="item.strCityId">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item prop="strAddr_area_id">
                            <el-select v-model="form.strAddr_area_id" filterable placeholder="">
                                <el-option  v-for="item in areas"  :label="item.strAreaName"  :value="item.strAreaId" :key="item.strAreaId">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item prop="strDetailAddress">
                           <el-input v-model="form.strDetailAddress" placeholder=""></el-input>
                        </el-form-item>
                    </el-col>
                </el-form-item>

                <el-form-item label="">
                    <el-select v-model="saleAddsId.strSaleProvinceId" filterable placeholder="">
                        <el-option label="" value="" ></el-option>
                        <el-option  v-for="item in saleAdds.provinces"  :label="item.strProvinceName"  :value="item.strProvinceId +','+ item.strProvinceName"  :key="item.strProvinceId">
                        </el-option>
                    </el-select>
                    <el-select v-model="saleAddsId.strSaleCityId" filterable placeholder="">
                        <el-option label="" value="" ></el-option>
                        <el-option  v-for="item in saleAdds.citys"  :label="item.strCityName"  :value="item.strCityId +','+ item.strCityName" :key="item.strCityId">
                        </el-option>
                    </el-select>
                    <el-select v-model="saleAddsId.strSaleAreaId" filterable placeholder="">
                        <el-option label="" value="" ></el-option>
                        <el-option  v-for="item in saleAdds.areas"  :label="item.strAreaName"  :value="item.strAreaId +','+ item.strAreaName" :key="item.strAreaId">
                        </el-option>
                    </el-select>
                    <el-button type="primary" icon="plus" size="small" v-on:click = "addSaleAddr()"></el-button>
                    <div >

                        <p class="add-addr-title"></p>
                        <div class="addr-remind" v-show = 'addSaleList.length == 0'></div>
                        <div class="addr-container">
                            <p class='add-addr-item' v-for = "(item , index) in addSaleList" v:key="index">
                                <el-col :span="18">
                                    <span >{{ item.saleName }}</span>
                                </el-col>
                                <el-col :span="6" class = 'submitRightNoM'>
                                    <el-button size="mini" v-on:click="delSaleAddr(index)"> </el-button>
                                </el-col>
                            </p>
                        </div>

                        <!-- <p class="add-addr-title"></p>
                        <div class="addr-remind" v-show = 'addSaleList.length == 0'></div>
                        <div class="addr-container">
                            <p class='add-addr-item' v-for = "(item , index) in addSaleList" v:key="index"><span class='sale_addr_name'>{{ item.saleName }}</span><a class='am-btn admin-float-right' v-on:click="delSaleAddr(index)"></a></p>
                        </div> -->
                    </div>
                </el-form-item>


                <el-form-item label="" prop = 'strLicense_num'>
                    <el-input v-model="form.strLicense_num" placeholder='' disabled></el-input>
                </el-form-item>
                <el-form-item label="" prop = 'strPrefix_str'>
                    <el-input v-model="form.strPrefix_str" placeholder='24()+4()'></el-input>
                </el-form-item>
                <el-form-item label="S4">
                    <el-select v-model="form.strConnection_info" placeholder="">
                        <el-option  v-for="item in channelUserList"  :label="item.strUserName + '/' + item.strUserTel"  :value="item.strUserId" :key="item.strUserId">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="">
                    <el-input v-model="form.strMonth_sales"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="form.strScore" placeholder="">
                        <el-option label="A" value="0"></el-option>
                        <el-option label="B" value="1"></el-option>
                        <el-option label="C" value="2"></el-option>
                        <el-option label="D" value="3"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="form.strValuationId" placeholder="">
                        <el-option label="2B" value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                        <el-form-item  label="%" prop='strPercent' >
                            <el-col :span="4">
                                <el-input type="number" v-model="form.strPercent"></el-input>
                            </el-col>
                        </el-form-item>
                        <br>
                        <el-form-item label="" prop='strApplyMax' >
                            <el-col :span="4">
                                <el-input type="number"  v-model="form.strApplyMax"></el-input>
                            </el-col>
                        </el-form-item>
                </el-form-item>
                <el-form-item label="">
                    <el-button type="primary" size="small" v-model="form.strShopAwardId">+</el-button>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="form.strPayMethodId" placeholder="">
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="/APP">
                    <el-checkbox-group v-model="form.selfFunction">
                        <el-checkbox label="" ></el-checkbox>
                        <el-checkbox label=""></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>

                <el-form-item class = 'submitRight'>
                    <el-button type="primary" @click="submitnow('form')"></el-button>
                    <el-button @click="cancelnow"></el-button>
                </el-form-item>
                
            </el-form>
        </el-tab-pane>
        <el-tab-pane label="">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                  <el-input v-model="filters.strStoreId" placeholder="ID" style="min-width: 240px;"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-input v-model="filters.strStoreName" placeholder="" style="min-width: 240px;"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="search"></el-button>
                </el-form-item>
            </el-form>

            <div class="tool">
                <router-link to="add_store"><el-button type="primary" size="small"></el-button></router-link>
            </div>
            <el-table
                :data="dataList"
                border
                style="width: 100% ; min-height:300px">
                <el-table-column prop="strStoreId" label="ID" >
                    
                </el-table-column>
                <el-table-column prop="strStoreName" label="" ></el-table-column>
                <el-table-column prop="strProvinceName" label="">
                    <template scope="scope">
                        {{scope.row.strProvinceName + scope.row.strCityName + scope.row.strAreaName +scope.row.strAddress}}
                    </template>
                </el-table-column>
                <el-table-column prop="strChannelName" label="" ></el-table-column>
                <el-table-column prop="strChannelManagerName" label="" ></el-table-column>
                <el-table-column  label="" >
                    <template scope="scope">
                        {{scope.row.strStatus == 1 ?'':''}}
                    </template>
                </el-table-column>
                <el-table-column 
                    label=""
                    >
                    <template scope="scope">
                        <el-button class = 'indexFunBtn' type="primary" @click="editStore(scope.row.strStoreId)"  size="small"></el-button>
                        <el-button class = 'indexFunBtn' type="danger" @click="disableStore(scope.row.strStoreId)"  size="small"></el-button>
                        <el-button class = 'indexFunBtn' type="primary" @click="enableStore(scope.row.strStoreId)"  size="small"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage4"
                  :page-sizes="[10, 20, 30, 40]"
                  :page-size="10"
                  layout="total, prev, pager, next, jumper"
                  :total="total">
                </el-pagination>
            </div>
        </el-tab-pane>
        <el-tab-pane label="">
        </el-tab-pane>
    </el-tabs>
</div>
</div>
</template>
<style type="text/css" scope>
    .channelInfo{width: 1000px}
</style>
<script>
    import api from '../../api/api'
    import util from '../../common/util'
  export default {
    data() {
        
        var validateEmployeeNum = (rule, value, callback) => {

            if(!/^(([a-zA-Z]{2})|([a-zA-Z]{4}))\d{2,4}$/g.test(value)){
                callback(new Error('24()+4()'))
            }
            callback()
        }
        
        return {
            //
            addS3Item:{},
            provinceChange : false ,
            cityChange : false ,
            form: {
              strShareId:'',
              strLevelId: '',

              strFullName: '',
              strPartner_id: '',
              strSecond_channel:'',
              strAddr_province_id: '',
              strAddr_city_id: '',
              strAddr_area_id: '',
              strDetailAddress: '',
              strSale_addr:'',
              strStatus:'1',
              
              strLicense_num: '',
              strPrefix_str: '',
              strConnection_info: '',
              strMonth_sales: '',
              strChannelPic:'',
              strScore: '1',
              strValuationId: '',
              strPercent: '',
              strApplyMax: '',
              strShopAwardId: '',

              strPayMethodId: '',
              selfFunction: [],
              strGreen_recycle:'0',
              strGather_userInfo:'0',
              strChannelId:''

            },
            saleAddsId: {
                strSaleProvinceId : '',
                strSaleCityId : '',
                strSaleAreaId : ''
            },
            channelTemplateList:[],
            partnerList:[],
            provinces:[],
            citys:[],
            areas:[],
            partnerList:[],
            saleAdds:{
                provinces:[],
                citys:[],
                areas:[]
            },
            addSaleList :[],
            channelUserList :[],
            defaultDate:'',//
            rules:{
                'strAddr_province_id': [
                    {  required: true, message: '', trigger: 'change' }
                ],
                'strAddr_city_id': [
                    {  required: true, message: '', trigger: 'change' }
                ],
                'strAddr_area_id': [
                    {  required: true, message: '', trigger: 'change' }
                ],
                'strDetailAddress': [
                    {  required: true , validator: util.validateAddr, trigger: 'blur' }
                ],
                'strFullName': [
                    {  required: true , validator: util.validateName, trigger: 'blur' }
                ],
                'strLicense_num': [
                    {  required: true , validator: util.validateLicenseNum, trigger: 'blur' }
                ],
                'strPrefix_str': [
                    {  required: true , validator: validateEmployeeNum, trigger: 'blur' }
                ],
                'strPercent': [
                    {  required: true, message: '', trigger: 'blur' }
                ],
                'strApplyMax': [
                    {  required: true, message: '', trigger: 'blur' }
                ]
            },
            //
            dataList:[],
            pageIndex:'0',
            pageSize:'10',
            currentPage4:1,
            total:0,
            filters:{
                'strStoreId':'',
                'strStoreName':''
            }

        }
    },
    watch: {
        // 
        'form.strAddr_province_id': function(val, oldVal) {
            this.form.strAddr_city_id = ''
            this.getCitys(val , this.provinceChange)
            this.provinceChange = true
        },
        'form.strAddr_city_id': function(val) {
            this.form.strAddr_area_id = ''
            this.getAreas(val,this.cityChange)
            this.cityChange = true
        },
        // 
        'saleAddsId.strSaleProvinceId': function(val, oldVal) {
            this.saleAddsId.strSaleCityId = ''
            this.getCitys(val.split(',')[0] ,false, 'sale')
        },
        'saleAddsId.strSaleCityId': function(val) {
            this.saleAddsId.strSaleAreaId = ''
            this.getAreas(val.split(',')[0] ,false, 'sale')
        }
    },
    mounted()  {
        this.getChannelId()
        // this.loadInfo()
        this.getDefaultDate(this.form.strChannelId)

        this.showList() //
    },
    methods: {
        getChannelId:function(){
            this.form.strChannelId= this.$route.query.id
        },
        submitnow(formName) {
            var self = this
            for(var i in this.form.selfFunction){
                if(this.form.selfFunction[i] == '') this.form.strGather_userInfo = '1'
                if(this.form.selfFunction[i] == '') this.form.strGreen_recycle = '1'
            }
            let saleList = []
            for(var i in this.addSaleList){
                saleList.push(this.addSaleList[i].saleId)
            }
            this.form.strSale_addr = saleList
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    api.addChannelLogic(this.form).then(res => {
                        if (res.ret != '0') {
                            this.$layer.alert(res.retinfo)
                            return
                        }
                        this.$message('')
                        self.$router.push({ path: '/channel/index' })
                    })
                } else {
                    console.log('error submit!!')
                    return false
                }
            })
        },
        loadInfo: function() {
            api.getAddress({}).then(res => {
                if (res.ret != '0') {
                    this.$layer.alert(res.retinfo)
                    return
                }
                this.provinces = res.data.address
                this.saleAdds.provinces = res.data.address
                this.form.strAddr_province_id = this.defaultDate.strAddrProvinceId
            }),
            api.getChannelTemplate({}).then(res => {
                if (res.ret != '0') {
                    this.$layer.alert(res.retinfo)
                    return
                }
                this.channelTemplateList = res.data.channelTemplateList
                this.form.strLevelId = this.defaultDate.strLevelId

            }),
            api.getPartner({}).then(res => {
                if (res.ret != '0') {
                    this.$layer.alert(res.retinfo)
                    return
                }
                this.partnerList = res.data.partnerList
                this.form.strPartner_id = this.defaultDate.strPartnerId

            }),
            api.getAllS1({}).then(res => {
                if (res.ret != '0') {
                    this.$layer.alert(res.retinfo)
                    return
                }
                this.channelUserList = res.data.listUsers
                this.channelUserList.push({strUserName :'' ,strUserTel:'' , strUserId:'0'})
                if(this.addS3Item.strUserId) {
                    this.channelUserList.push(this.addS3Item)
                    this.form.strConnection_info =  this.addS3Item.strUserId
                }
                this.form.strConnection_info = this.defaultDate.strS3Id

            })
            
        },
        // 
        getDefaultDate : function(channelId){
            api.getChannelInfo({channelId:channelId}).then(res => {
                if (res.ret != '0') {
                    this.$layer.alert(res.retinfo)
                    return
                }
                this.defaultDate = res.data
                
                this.addS3Item = {
                    'strUserName':this.defaultDate.strS3Name,
                    'strUserTel':this.defaultDate.strS3Phone,
                    'strUserId':this.defaultDate.strS3Id
                },
               
                this.form.strShareId = this.defaultDate.strShareId
                this.form.strFullName = this.defaultDate.strChannelName
                this.form.strLicense_num = this.defaultDate.strLicenseNum
                this.form.strPrefix_str = this.defaultDate.strPrefix
                this.form.strMonth_sales = this.defaultDate.strMonthSales
                this.form.strPercent = this.defaultDate.strSharePercent
                this.form.strApplyMax = this.defaultDate.strAppleMax
                this.form.strDetailAddress = this.defaultDate.strAddress
                this.form.strSecond_channel = this.defaultDate.strSecondChannel

                this.form.strValuationId = this.defaultDate.strPlatformTypeId
                this.form.strPayMethodId = this.defaultDate.strPayId

                if(this.defaultDate.strIsCollect == '1') this.form.selfFunction.push('')
                if(this.defaultDate.strIsRecycle == '1') this.form.selfFunction.push('')

                var saleAdds = this.defaultDate.saleAreaList

                for(var i in saleAdds){
                    if(!saleAdds[i].addr_province_name) saleAdds[i].addr_province_name = ''
                    if(!saleAdds[i].addr_city_name) saleAdds[i].addr_city_name = ''
                    if(!saleAdds[i].addr_area_name) saleAdds[i].addr_area_name = ''
                    var saleName = saleAdds[i].addr_province_name + ' ' + saleAdds[i].addr_city_name+ ' ' + saleAdds[i].addr_area_name
                    var saleId = {
                        'addr_province_id' : saleAdds[i].addr_province_id ,
                        'addr_city_id' : saleAdds[i].addr_city_id ,
                        'addr_area_id' : saleAdds[i].addr_area_id 
                    }
                    this.addSaleList.push({'saleName':saleName , 'saleId':saleId})
                }
            }).then(res => {this.loadInfo()})
        },
        // 
        getCitys: function(val ,change, sale) {
            if(sale == 'sale'){
                var saleProvinces = this.saleAdds.provinces
                for (var index in saleProvinces) {
                    if (saleProvinces[index].strProvinceId == val) {
                        this.saleAdds.citys = saleProvinces[index].citys
                    }
                }
                return
            }else{
                for (var index in this.provinces) {
                    if (this.provinces[index].strProvinceId == val) {
                        this.citys = this.provinces[index].citys
                        if(!change) this.form.strAddr_city_id = this.defaultDate.strAddrCityId
                    }
                }
            }
        },
        // 
        getAreas: function(val ,change, sale) {
            if(sale == 'sale'){
                var saleCity = this.saleAdds.citys
                for (var index in saleCity) {
                    if (saleCity[index].strCityId == val) {
                        this.saleAdds.areas = saleCity[index].areas
                    }
                }
            }else{
                for (var index in this.citys) {
                    if (this.citys[index].strCityId == val) {
                        this.areas = this.citys[index].areas
                        if(!change) this.form.strAddr_area_id = this.defaultDate.strAddrAreaId
                    }
                }
            }
        },

        //
        addSaleAddr: function(){
            let provinceName = this.saleAddsId.strSaleProvinceId.split(',')[1] ? this.saleAddsId.strSaleProvinceId.split(',')[1] :''
            let cityName = this.saleAddsId.strSaleCityId.split(',')[1] ? this.saleAddsId.strSaleCityId.split(',')[1] :''
            let areaName = this.saleAddsId.strSaleAreaId.split(',')[1] ? this.saleAddsId.strSaleAreaId.split(',')[1] :''
            var willAddSale = {
                'saleName':provinceName+' / '+cityName+' / '+areaName ,
                'saleId':{
                    'addr_province_id' : this.saleAddsId.strSaleProvinceId.split(',')[0] ,
                    'addr_city_id' : this.saleAddsId.strSaleCityId.split(',')[0] ,
                    'addr_area_id' : this.saleAddsId.strSaleAreaId.split(',')[0] 
                }
            }
            // 
            for(var i in this.addSaleList){
                if(this.addSaleList[i].saleId.addr_province_id == willAddSale.saleId.addr_province_id
                    && this.addSaleList[i].saleId.addr_city_id == willAddSale.saleId.addr_city_id
                    && this.addSaleList[i].saleId.addr_area_id == willAddSale.saleId.addr_area_id
                    ){
                        this.$layer.alert('')
                        return
                }
            }

            this.addSaleList.push(willAddSale)
        },
        //
        delSaleAddr: function(index){
            this.addSaleList.splice(index,1)
        },
        //
        cancelnow: function() {
            this.$router.push({ path: '/channel/index' })
        },


        //
        handleSizeChange(val) {
            console.log(` ${val} `)
        },
        handleCurrentChange(val) {
            this.pageIndex = (val- 1) * 10
            this.showList()
        },
        showList:function(){
            let data ={
                pageinfo:{
                    'pageIndex':String(this.pageIndex),
                    'pageSize':this.pageSize,
                },
                'searchkeys':this.filters,
                "strChannelId":this.form.strChannelId
            }
            api.getChannelStoreList(data).then(res => {
                if (res.ret != '0') {
                    this.$layer.alert(res.retinfo)
                    return
                }
                this.dataList = res.data.list
                this.dataList.forEach( item => {
                    item.strChannelName = this.form.strFullName //
                })
                this.total = res.data.nums
            })
        },
        //
        editStore:function(strStoreId){
            this.$router.push({
                name:'editStore',
                query:{id:strStoreId}
            })
        },
        // 
        disableStore:function(strStoreId){
            api.disableStore({strStoreId:strStoreId}).then(res => {
                if (res.ret != '0') {
                    this.$layer.alert(res.retinfo)
                    return
                }
                this.$message({
                    message: '',
                    type: 'success'
                });
                this.showList()
            })

        },
        enableStore:function(strStoreId){
            api.enableStore({strStoreId:strStoreId}).then(res => {
                if (res.ret != '0') {
                    this.$layer.alert(res.retinfo)
                    return
                }
                this.$message({
                    message: '',
                    type: 'success'
                });
                this.showList()
            })

        },
        //search
        search:function(){
            this.showList()
        }


    }
  }
</script>


